/* eslint-disable react/prop-types */
/* eslint-disable react/display-name */

/**
 * CSS imports
 ============================================================================ */
import "./Transfer.css";

/**
 * System imports
 ============================================================================ */
import React from "react";
import { useTranslation } from "react-i18next";
import { TransferToSelf, TransferToPerson, TransferToBusiness } from "./index";

/**
  * User imports
  ============================================================================ */

/**
 * Component: Transfer
 */
export default (args) => {
  /**
   * Default Properties
   */
  let defaults = { classSubmitButton: "transfer-submit-button" };

  /**
   * Properties
   */
  const props = Object.assign(defaults, args);

  // console.log("TRANSFER DATA", props);

  /**
   * Barrels
   */
  const { userData, transferType } = props.barrelGetters;
  const { setRedirectTo } = props.barrelSetters;
  const { onTransferTypeChange } = props.barrelEventHandlers;

  const { t } = useTranslation("Transfer");

  const type = transferType;
  // const onTypeChange = args.onTypeChange;

  // console.log("TRANSFER DATA TYPE:", type);

  // const [transferCard, setTransferCard] = useState(null);

  const onNavigationClick = (id) => {
    onTransferTypeChange(id);
  };

  const navigation = [
    {
      name: t("U2S"),
      link: "#u2s",
    },
    {
      name: t("U2U"),
      link: "#u2u",
    },
    {
      name: t("U2B"),
      link: "#u2b",
    },
  ];

  // console.log("TRANSFER DATA", data);
  // const title = t("Transfer to account");

  if (userData.uid === null) setRedirectTo("/sign-out");

  switch (type) {
    case "#u2u": // User to user
      // case "/u2u": // User to user
      return (
        <TransferToPerson
          title={t("Transfer to account")}
          classSubmitButton={props.classSubmitButton}
          args={props}
          navigation={navigation}
          navigationdefaultActiveKey={type}
          onNavigationClick={onNavigationClick}
        />
      );
    // break;
    case "#u2b": // User to business
      // case "/u2b": // User to business
      return (
        <TransferToBusiness
          title={t("Transfer to account")}
          classSubmitButton={props.classSubmitButton}
          args={props}
          navigation={navigation}
          navigationdefaultActiveKey={type}
          onNavigationClick={onNavigationClick}
        />
      );
    // break;
    default:
      // "#u2s"
      return (
        <TransferToSelf
          title={t("Transfer to account")}
          classSubmitButton={props.classSubmitButton}
          args={props}
          navigation={navigation}
          navigationdefaultActiveKey={type}
          onNavigationClick={onNavigationClick}
        />
      );
    // break;
  }
};
