/* eslint-disable react/prop-types */
/* eslint-disable react/display-name */
/**
 * CSS
 ============================================================================ */
import "./LinkList.css";

/**
 * System imports
 ============================================================================ */
import React from "react";
// import { useTranslation } from "react-i18next";

/**
 * User imports
 ============================================================================ */
import InjectChildren from "../injectChildren";

/**
 * Default export
 ============================================================================ */
export default (args) => {
  /**
   * Default Properties
   ============================================================================ */
  let defaults = {
    namespace: "LinkList",
    title: "Untitled",
  };

  /**
   * Properties
   ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
   * Functions
   ============================================================================ */

  /**
   * Return
   ============================================================================ */
  return (
    <div className="link-list">
      <h4>{props.title}</h4>
      <InjectChildren {...args} />
    </div>
  );
};
