/* eslint-disable react/prop-types */
/* eslint-disable react/display-name */
/**
 * CSS
 ============================================================================ */
import "./Security.css";

/**
  * System imports
  ============================================================================ */
import React from "react";
import { useTranslation } from "react-i18next";
import { Container, Row, Col } from "react-bootstrap";

/**
  * User imports
  ============================================================================ */
import Appetizer from "../appetizer";

/**
  * Default export
  ============================================================================ */
export default (args) => {
  /**
    * Default Properties
    ============================================================================ */
  let defaults = { namespace: "Security" };

  /**
    * Properties
    ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
    * Module vars
    ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  /**
    * Functions
    ============================================================================ */

  /**
    * Return
    ============================================================================ */
  return (
    <div className="security" style={{ margin: "2rem 1rem" }}>
      <Container fluid>
        <Row>
          <Col>
            <h2 style={{}}>Security for buyers</h2>
          </Col>
        </Row>
        <Row>
          <Col sm>
            <Appetizer
              className="appetizer-security"
              title={t("Your financial information")}
              showLink={false}
              transKey="Security:Your financial information"
              {...args}
            >
              Local-Currency helps keep your transactions secure by not sharing
              your full financial information with sellers.
            </Appetizer>
          </Col>
          <Col sm>
            <Appetizer
              className="appetizer-security"
              title={t("Secure technology")}
              showLink={false}
              transKey="Security:Secure technology"
              {...args}
            >
              Our encryption help keeps your online transactions guarded from
              start to finish.
            </Appetizer>
          </Col>
          <Col sm>
            <Appetizer
              className="appetizer-security"
              title={t("Purchase protection around the globe")}
              showLink={false}
              transKey="Security:Purchase protection around the globe"
              {...args}
            >
              Buy or sell around the globe. We process local currencies in all
              regional markets to make sending, spending, and selling simple and
              secure.
            </Appetizer>
          </Col>
        </Row>
      </Container>
    </div>
  );
};
