/* eslint-disable react/prop-types */
/* eslint-disable react/display-name */
/**
 * CSS
 ============================================================================ */
import "./Newsroom.css";

/**
  * System imports
  ============================================================================ */
import React from "react";
import { useTranslation } from "react-i18next";
import { Container, Row, Col } from "react-bootstrap";

/**
  * User imports
  ============================================================================ */
import Card from "../card";
import Appetizer from "../appetizer";

/**
  * Default export
  ============================================================================ */
export default (args) => {
  /**
    * Default Properties
    ============================================================================ */
  let defaults = { namespace: "Newsroom", onNavigationClick: () => {} };

  /**
    * Properties
    ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
    * Barrels
    ============================================================================ */
  const { helpPath1 } = props.barrelGetters;
  // const {} = props.barrelSetters;
  const { setHelpPath } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;
  const { pathNewsCategory } = props.barrelProps;

  /**
    * Module vars
    ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  /**
    * Functions
    ============================================================================ */
  const navigation = [
    {
      name: t("Home"),
      link: "/news-category?cat=home&search=",
    },
    {
      name: t("Latest News"),
      link: "/news-category?cat=latest-news&search=",
    },
    {
      name: t("About Us"),
      link: "/news-category?cat=about-us&search=",
    },
  ];

  /**
    * Return
    ============================================================================ */
  return (
    <div className="newsroom" style={{ margin: "2rem 1rem" }}>
      <Card
        title={false}
        navigation={navigation}
        navigationdefaultActiveKey={helpPath1}
        onNavigationClick={(link) => setHelpPath(1, link)}
        headerPadding="0 20px 10px 20px"
        bodyPadding="20px 20px 10px 20px"
        border={0}
        headerBorderBottom={
          props.navigation === false ? 0 : "1px solid rgba(0,0,0,.125)"
        }
        maxWidthBody="100%"
      >
        <h1>{t("Latest News")}</h1>
        <Container fluid>
          <Row>
            {/*
             * Newsroom Appetizer 1
             */}
            <Col sm>
              <Appetizer
                className="appetizer-newsroom"
                title={t("Local-Currency Goes Online")}
                href={`/${pathNewsCategory}?search=`}
                linkText={t("View all news")}
                transKey="Newsroom:Appetizer 1"
                {...args}
              >
                We are pleased to announce the launch of beta version 0.8 of
                local-currency.com! Our goal with this platform is to provide
                our customers and merchants with an easier way to join local and
                regional currency markets. The updated site includes changes to
                its navigation, with dedicated menus for both mobile and desktop
                versions.
              </Appetizer>
            </Col>

            {/*
             * Newsroom Appetizer 2
             */}
            <Col sm>
              <Appetizer
                className="appetizer-newsroom"
                title={t(
                  "Business Registration for Local Markets Now Available"
                )}
                href={`/${pathNewsCategory}?search=`}
                linkText={t("View all news")}
                transKey="Newsroom:Appetizer 2"
                {...args}
              >
                Starting a business in a local currency market is now just
                clicks away. Register your business, including all the products
                and services you wish to offer your customers, and
                Local-Currency platform will generate a local business number
                (B-number) and all the product numbers (P-numbers) for you.
              </Appetizer>
            </Col>

            {/*
             * Newsroom Appetizer 3
             */}
            <Col sm>
              <Appetizer
                className="appetizer-newsroom"
                title={t("How to Keep Regional Economy Afloat During COVID-19")}
                href={`/${pathNewsCategory}?search=`}
                linkText={t("View all news")}
                transKey="Newsroom:Appetizer 3"
                {...args}
              >
                When small business owners first heard about the COVID-19
                pandemic, they didnt think it would come knocking on their
                doors. They are resilient native...
              </Appetizer>
            </Col>
          </Row>
        </Container>
      </Card>
    </div>
  );
};
