/* eslint-disable react/prop-types */
/* eslint-disable react/display-name */
/**
 * System imports
 ============================================================================ */
import "./PublishedSwitch.css";

/**
 * System imports
 ============================================================================ */
import React from "react";
// import { useTranslation } from "react-i18next";
import { Form } from "react-bootstrap";

/**
 * User imports
 ============================================================================ */
// import Card from "../card";

/**
 * Default export
 ============================================================================ */
export default (args) => {
  /**
   * Default Properties
   ============================================================================ */
  let defaults = {
    namespace: "PublishedSwitch",
    label: "Publish",
    isPublished: false,
    onChange: () => {},
  };

  /**
   * Properties
   ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
   * Barrels
   ============================================================================ */
  // const {} = props.barrelGetters;
  // const {} = props.barrelSetters;
  // const {} = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
   * Module vars
   ============================================================================ */
  // const { t } = useTranslation([props.namespace]);

  /**
   * Functions
   ============================================================================ */
  const toggleMode = () => {
    props.onChange(!props.isPublished);
  };

  // console.log("props.isPublished:", props.isPublished);

  /**
   * Return
   ============================================================================ */
  return (
    <>
      <Form.Check
        type="switch"
        id="switchPublished"
        custom
        className="variant-dark"
        label={props.label}
        checked={props.isPublished}
        onChange={toggleMode}
      />
    </>
  );
};
