/* eslint-disable react/prop-types */
/* eslint-disable react/display-name */
/**
 * CSS
 ============================================================================ */
// import "bootstrap/dist/css/bootstrap.min.css";
import "./Main.css";

/**
 * System imports
 ============================================================================ */
import React, { useEffect } from "react";
// import { useTranslation } from "react-i18next";
import { Container, Row, Col } from "react-bootstrap";

/**
 * User imports
 ============================================================================ */
import { useTimeout } from "../utils/useTimeout";
// import { useScrollPosition } from "../utils/useScrollPosition";
import Routes from "../routesOuter";
import Leads from "../leads";
import Toasts from "../toasts";
// import ConsoleWarning from "../consoleWarning";
import { useConsoleWarning } from "../utils/console";

/**
 * Component: Main
 ============================================================================ */
export default (args) => {
  /**
    * Default Properties
    ============================================================================ */
  let defaults = { namespace: "Main" };

  /**
      * Properties
      ============================================================================ */
  const props = Object.assign(defaults, args);
  const barrels = args;

  /**
   * Module vars
   ============================================================================ */
  const now = new Date();

  /**
      * Barrels
      ============================================================================ */
  const {
    session,
    serverResponseReceived,
    signedIn,
    userData,
    dailyAllowanceDispatched,
    language,
    cartID,
    cartContent,
    validateTokenEmail,
    passwordResetTokenValidated,
    validateTokenPasswordReset,
    connected,
    // isExternalRequest,
    clearRefs,
    helpPath2,
  } = props.barrelGetters;
  const {
    setRequestTimedOut,
    // setUpdateInterval,
    setDailyAllowanceDispatched,
    setRequest,
    // setIsExternalRequest,
    setUserEditorFirstname,
    setUserEditorLastname,
    setUserEditorEmail,
    setUserEditorPassword,
    // Currency registration
    setCurrencyCode,
    setCurrencyName,
    setCurrencyRegion,
    setCurrencyNewRate,

    // Business registration
    setBusinessRegistrationAddTitle,
    setBusinessRegistrationAddDescription,

    // Purpose registration
    setPurposeRegistrationTitle,
    setPurposeRegistrationDescription,
    setPurposeRegistrationKeywords,

    // Contact
    setContactSubject,
    setContactMessage,

    // Date range
    setDateRangeStart,
    setDateRangeEnd,

    // Search
    setSearch,

    //
    setClearRefs,

    // setHideOnScroll,
  } = props.barrelSetters;
  const {
    getIP,
    getCartPurposes,
    getAccountData,
    sendRequest,
    sendEmailValidationRequest,
    sendPasswordResetRequest,
    showMessage,
    getCart,
    loadContextFiles,
    getTopics,
    getBusinesses,
    getTextsList,
    getUrlParam,
    // showConsoleWarning,
  } = props.barrelFunctions;
  const {
    onRedirect,
    onSessionStart,
    // onSessionEnd,
  } = props.barrelEventHandlers;
  const {
    pathJobCategory,
    pathHelpCategory,
    pathNewsCategory,
  } = props.barrelProps;

  /** 
   * Custom hooks
   ============================================================================ */

  /** 
   * Effect hooks
   ============================================================================ */

  /**
   * On mount / on unmount
   */
  useEffect(() => {
    // showConsoleWarning();
    useConsoleWarning();

    getIP();

    return () => {
      // on unmount
    };

    // eslint-disable-next-line
  }, []);

  /**
   * On language change
   */
  useEffect(() => {
    loadContextFiles(language);

    // eslint-disable-next-line
  }, [language]);

  /**
   * On session ID change
   */
  useEffect(() => {
    if (session === null) return;

    onSessionStart();

    // eslint-disable-next-line
  }, [session]);

  /**
   * On connected
   */
  useEffect(() => {
    // eslint-disable-next-line
  }, [connected]);

  /**
   * On request timeout
   */
  useTimeout(
    () => {
      setRequestTimedOut(true);
      setRequest(null);
      showMessage("REQUEST_EXPIRED", "info");
    },
    serverResponseReceived === false ? 10000 : -1 // 10 sec till socket request timeout
  );

  /**
   * Map user data to user editor vars
   */
  useEffect(() => {
    if (userData === null) return;
    setUserEditorFirstname(userData.firstname);
    setUserEditorLastname(userData.lastname);
    setUserEditorEmail(userData.email);
    setUserEditorPassword("");
    // eslint-disable-next-line
  }, [JSON.stringify(userData)]);

  /**
   * On signed in status change
   */
  useEffect(() => {
    if (!signedIn) return;

    // setUpdateInterval(-1);

    // Dispatch daily allowance
    // TODO: consider including dailyAllowanceDispatched as parameter for the effect
    if (
      userData.uid !== null &&
      userData.uid !== "none" &&
      !dailyAllowanceDispatched
    ) {
      getAccountData();
      sendRequest({
        req: "dispatch daily allowance",
        data: { uid: userData.uid },
      });
      setDailyAllowanceDispatched(true);
    }

    /**
     * Handle external request
     */
    // let redirectTarget;
    if (userData.uid !== null && userData.uid !== "none") {
      if (cartID !== null) {
        getCart();
        onRedirect("/auth");
      }
    } else {
      if (window.location.pathname !== "/sign-out") {
        if (
          userData === null ||
          userData.status !== 1 ||
          typeof userData.status === "undefined"
        ) {
          showMessage("EMAIL_NOT_YET_VALIDATED", "info");
          onRedirect("/sign-out");
          return;
        } else {
          onRedirect("/account-registration");
        }
      }
    }

    // eslint-disable-next-line
  }, [signedIn]);

  /**
   * On account number change
   */
  useEffect(() => {
    // if (accountNumber === null) return;

    getCart();

    // eslint-disable-next-line
  }, [userData]); // TODO: Check if this works like the accountnumber

  /**
   * Fetch data according to the location pathname
   */
  useEffect(() => {
    // Check full paths
    switch (window.location.pathname) {
      case `/${pathNewsCategory}`:
        getTopics({ newPage: 1, category: pathNewsCategory });
        getTextsList({
          newPage: 1,
          // category: helpPath2,
        });
        break;

      case `/${pathHelpCategory}`:
        getTopics({ newPage: 1, category: pathHelpCategory });
        getTextsList({
          newPage: 1,
          // category: helpPath2,
        });
        break;

      case `/${pathJobCategory}`:
        getTopics({
          newPage: 1,
          category: pathJobCategory,
        });
        getTextsList({
          newPage: 1,
          // category: helpPath2,
        });
        break;

      case "/business":
        getBusinesses(1);
        break;

      case "/job":
      case "/help":
      case "/release":
        // const link = getUrlParam("t");

        // Get text
        sendRequest({
          req: "get text",
          data: {
            textid: getUrlParam("t"),
            language,
          },
        });

        break;

      case "/jobs":
        getTopics({ newPage: 1 });
        getTextsList({ newPage: 1 });
        // setHelpPath(2, "");
        break;

      case "/newsroom":
        getTopics({ newPage: 1 });
        getTextsList({ newPage: 1 });
        // setHelpPath(2, "");
        break;

      case "/about":
        // Get about

        sendRequest({
          req: "get text",
          data: {
            textid: "T14916817101214679",
            language,
          },
        });
        break;

      default:
        break;
    }

    // eslint-disable-next-line
  }, [window.location.pathname]);

  /**
   * Set the search hook when the window location search changes
   */
  useEffect(() => {
    const paramSearch = getUrlParam("search");
    if (paramSearch !== null) {
      setSearch(paramSearch);
    } else {
      setSearch("");
    }

    // eslint-disable-next-line
  }, [window.location.search]);

  useEffect(() => {
    // console.log("helpPath2: ", helpPath2);
    // eslint-disable-next-line
  }, [helpPath2]);

  /**
   * Clear refs
   */
  useEffect(() => {
    if (clearRefs === false) return;

    // Business registration
    setBusinessRegistrationAddTitle("");
    setBusinessRegistrationAddDescription("");

    // Purpose registration
    setPurposeRegistrationTitle("");
    setPurposeRegistrationDescription("");
    setPurposeRegistrationKeywords("");

    // Currency registration
    setCurrencyCode("");
    setCurrencyName("");
    setCurrencyRegion("");
    setCurrencyNewRate("");

    // Contact
    setContactSubject("");
    setContactMessage("");

    // Date range
    setDateRangeStart(now.setDate(now.getDate() - 7));
    setDateRangeEnd("today");

    // Search
    setSearch("");

    setClearRefs(false);

    // eslint-disable-next-line
  }, [clearRefs]);

  /**
   * On cart content change
   */
  useEffect(() => {
    if (!signedIn || cartContent === null) return;

    getCartPurposes();

    // eslint-disable-next-line
  }, [JSON.stringify(cartContent)]);

  /**
   * On email validation token
   */
  useEffect(() => {
    if (validateTokenEmail === null) return;

    sendEmailValidationRequest(JSON.parse(validateTokenEmail));

    // eslint-disable-next-line
  }, [validateTokenEmail]);

  /**
   * On email validation token
   */
  useEffect(() => {
    if (passwordResetTokenValidated === null) return;

    onRedirect("/password");

    // eslint-disable-next-line
  }, [passwordResetTokenValidated]);

  /**
   * On password reset validation token
   */
  useEffect(() => {
    if (validateTokenPasswordReset === null) return;

    sendPasswordResetRequest(JSON.parse(validateTokenPasswordReset));

    // eslint-disable-next-line
  }, [validateTokenPasswordReset]);

  const getJSX = () => {
    return (
      <>
        <Container fluid>
          <Row>
            {/** Left column */}
            <Col xl={4} lg={6} className="d-none d-lg-block" style={{}}>
              <Leads {...barrels} />
            </Col>

            {/** Middle column */}
            <Col
              lg={6}
              md={12}
              style={{ padding: 0, backgroundColor: "Transparent" }}
            >
              <Routes {...barrels} />
            </Col>

            {/** Right column */}
            <Col
              className="d-none d-xl-block"
              style={{
                paddingLeft: 0,
                paddingRight: 0,
              }}
            >
              <Toasts {...barrels} />
            </Col>
          </Row>
        </Container>
      </>
    );
  };

  return <>{getJSX()}</>;
};
